/* eslint-disable no-param-reassign */
/* eslint-disable no-underscore-dangle */
import React from 'react';
import Select from '../../components/Inputs/Select';

import InputText from '../../components/Inputs/Text';

const UserForm = props => {
  return (
    <>
      <fieldset>
        <legend>Usuário</legend>
        <div>
          <label>Nome</label>
          <InputText
            name="name"
            value={props.data.name}
            onChange={value => {
              props.changeField('name', value);
            }}
          />
        </div>
        <div>
          <label>Senha</label>
          <InputText
            name="password"
            value={props.data.password}
            onChange={value => {
              props.changeField('password', value);
            }}
            disabled={!props.new}
          />
        </div>
        <div>
          <label>Email</label>
          <InputText
            name="email"
            value={props.data.email}
            onChange={value => {
              props.changeField('email', value);
            }}
          />
        </div>

        <div>
          <label>Permissao</label>

          <Select
            onChange={value => {
              props.changeField('role', value);
            }}
            name="role"
            value={props.data.role || 'adm'}
            options={[
              { label: 'Admin', value: 'adm' },
              { label: 'Moderador', value: 'mod' },
            ]}
          />
        </div>
      </fieldset>
    </>
  );
};

UserForm.defaultProps = {
  data: {},
  changeField: () => {},
};

export default UserForm;
