import React from 'react';

import { connect } from 'react-redux';

import ModalChoice from '../../components/Modal/choice';
import DefaultLayout from '../../layouts/default';
import * as PhrasesActions from '../../store/phrases/actions';
import * as PhrasesSelectors from '../../store/phrases/selectors';

import Phrase from '../../components/Phrase';

class Phrases extends React.Component {
  state = {
    moderate: {
      phrase: false,
      socialUser: false,
    },
  };

  componentDidMount() {
    this.props.getPhrase();
  }

  render() {
    return (
      <>
        <h3>Moderação</h3>
        <br />
        {this.props.phrase && Object.keys(this.props.phrase).length > 0 ? (
          <div>
            <Phrase
              text={this.props.phrase.phrase}
              icon="fa-quote-right"
              onChange={phrase => {
                this.setState({
                  moderate: { ...this.state.moderate, phrase },
                });
              }}
              value={this.state.moderate.phrase}
            />

            {this.props.phrase.socialUser && (
              <Phrase
                text={this.props.phrase.socialUser}
                icon="fa-at"
                onChange={socialUser => {
                  this.setState({
                    moderate: { ...this.state.moderate, socialUser },
                  });
                }}
                value={this.state.moderate.socialUser}
              />
            )}

            <button
              className="btn btn-lg btn-success"
              onClick={() => {
                this.setState({
                  showModalSend: true,
                });
              }}
            >
              ENVIAR
            </button>
          </div>
        ) : (
          <p>Nenhuma frase para moderar</p>
        )}

        {this.state.showModalSend && (
          <ModalChoice
            message="Deseja moderar a frase selecionada?"
            onConfirm={async () => {
              // eslint-disable-next-line no-underscore-dangle
              await this.props.moderate(
                this.props.phrase._id,
                this.state.moderate
              );

              this.setState(
                {
                  showModalSend: false,
                  moderate: {
                    phrase: false,
                    socialUser: false,
                  },
                },
                () => {
                  this.props.getPhrase();
                }
              );
            }}
            onCancel={() => {
              this.setState({ showModalSend: false });
            }}
          />
        )}
      </>
    );
  }
}

const mapStateToProps = state => ({
  phrase: PhrasesSelectors.getPhrase(state),
});
const mapDispatchToProps = dispatch => ({
  moderate: async (id, moderate) => {
    await dispatch(PhrasesActions.moderate(id, moderate));
  },
  getPhrase: () => {
    dispatch(PhrasesActions.getPhrase());
  },
});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Phrases)
);
