import queryString from 'query-string';
import { push } from 'connected-react-router';
import * as types from './actionTypes';
import { setLoading } from '../loading/actions';
import { setModal } from '../modal/actions';

export const listLoaded = data => ({
  type: types.LIST_USERS_LOADED,
  data,
  loading: false,
});

export const setUser = data => ({
  type: types.SET_USER,
  data,
});

export const getList = params => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));
    let res;
    try {
      res = await API.get(`/user?${queryString.stringify(params)}`);
      dispatch(listLoaded(res.data));
    } catch (e) {
      dispatch(setLoading(false));
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }
  };
};

export const createUser = user => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.post(`/user`, user);
      dispatch(push('/users'));
      dispatch(
        setModal({ message: 'Usuário criado com sucesso', title: 'Pronto' })
      );
    } catch (e) {
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }

    dispatch(setLoading(false));
  };
};

export const updateUser = (id, user) => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.put(`/user/${id}`, user);
      dispatch(push('/users'));
      dispatch(
        setModal({ message: 'Usuário atualizado com sucesso', title: 'Pronto' })
      );
    } catch (e) {
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }

    dispatch(setLoading(false));
  };
};
