import React from 'react';

import { connect } from 'react-redux';
import { push } from 'connected-react-router';
import moment from 'moment-timezone';
import DefaultLayout from '../../layouts/default';
import List from '../../components/List';
import Pagination from '../../components/List/Pagination';
import Search from '../../components/Search';
import ExportCSV from '../../components/ExportCSV';
import * as NumbersActions from '../../store/numbers/actions';
import * as NumbersSelectors from '../../store/numbers/selectors';

class Tickets extends React.Component {
  state = {
    pagination: {
      page: 1,
      limit: 10,
    },
    search: {},
  };

  componentDidMount() {
    this.getList();
  }

  getList = () => {
    this.props.getList({ ...this.state.pagination, ...this.state.search });
  };

  openFormEdit = ticket => {
    this.props.setNumber(ticket);
  };

  parseList = list => {
    let parsedList = [];
    if (list) {
      parsedList = list.map(item => ({
        ...item,
        numberCode: item.code,
        ...item.customer,
        ...item.ticket,
        createdAt: moment(item.createdAt || '')
          .tz('America/Sao_Paulo')
          .format('DD/MM/YYYY'),
        customerCpf: item.customer.cpf,
        customerEmail: item.customer.email,
        customerName: item.customer.name,
      }));
    }

    return parsedList;
  };

  parseCSV = list => {
    let parsedList = [...list];
    if (list) {
      parsedList = list.map(item => {
        const customer = { ...item.customer };
        delete item.customer;
        return {
          ...item,
          numberCode: item.code,
          ...item.ticket,
          createdAt: moment(item.createdAt || '')
            .tz('America/Sao_Paulo')
            .format('DD/MM/YYYY'),
          customerCpf: customer.cpf,
          customerEmail: customer.email,
          customerName: customer.name,
          customerTelephone: customer.telefone,
        };
      });
    }

    return parsedList;
  };

  render() {
    const maxPage = this.state.pagination.page * this.state.pagination.limit;
    const list = this.parseList(this.props.list.items);

    return (
      <>
        <Search
          placeholder="Digite um cpf"
          onSearch={cpf => {
            this.setState({ search: { 'customer.cpf': cpf } }, () => {
              this.getList();
            });
          }}
        />
        <ExportCSV URL="/luckyNumbers" parseList={this.parseCSV} />
        <List
          headers={[
            { title: 'Serie', field: 'serie' },
            { title: 'Codigo', field: 'numberCode' },
            { title: 'Codigo Cupom', field: 'code' },
            { title: 'cpf', field: 'customerCpf' },
            { title: 'Data de Criação', field: 'createdAt' },
          ]}
          items={list}
          onEdit={this.openFormEdit}
          title="Numeros da Sorte"
        />

        <div className="row">
          <div className="col-12">
            <div
              className="dataTables_paginate paging_simple_numbers"
              id="data-table-responsive_paginate"
            >
              <Pagination
                page={this.state.pagination.page}
                totalPages={Math.ceil(
                  this.props.list.total / this.state.pagination.limit
                )}
                changePage={page => {
                  this.setState(
                    { pagination: { ...this.state.pagination, page } },
                    () => {
                      this.props.getList(this.state.pagination);
                    }
                  );
                }}
              />
            </div>
          </div>

          <div className="col-12 d-flex justify-content-center">
            <div className="dataTables_info" id="data-table-responsive_info">
              {`Showing ${this.state.pagination.page} to ${maxPage *
                this.state.pagination.page} of ${
                this.props.list.total
              } entries`}
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => ({
  list: NumbersSelectors.getNumbersList(state),
});
const mapDispatchToProps = dispatch => ({
  getList: pagination => {
    const pag = { ...pagination };

    pag.skip = (pagination.page - 1) * pagination.limit;
    delete pag.page;
    dispatch(NumbersActions.getList(pag));
  },
  setNumber: async ticket => {
    await dispatch(NumbersActions.setCustomer(ticket));

    dispatch(push('/numbers/form'));
  },
});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Tickets)
);
