import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { ConnectedRouter } from 'connected-react-router';
import { useSelector } from 'react-redux';
import { history } from './store/Redux';

import * as LoginSelectors from './store/login/selectors';
import Views from './views';

const App = () => {
  const role = useSelector(LoginSelectors.getRole);

  return (
    <ConnectedRouter key="router" history={history}>
      <Switch>
        <Route
          key="login"
          name="Login"
          exact
          path="/"
          component={Views.Login}
        />
        {role === 'mod' ? (
          <Route
            key="phrasesmoderate"
            name="phrasesmoderate"
            exact
            path="/phrases/moderate"
            component={Views.PhrasesModerate}
          />
        ) : (
          <>
            <Route
              key="dashboard"
              name="dashboard"
              exact
              path="/dashboard"
              component={Views.Home}
            />
            <Route
              key="users"
              name="users"
              exact
              path="/users"
              component={Views.Users}
            />
            <Route
              key="usersform"
              name="usersform"
              exact
              path="/users/form"
              component={Views.UsersForm}
            />
            <Route
              key="faq"
              name="Faq"
              exact
              path="/faq"
              component={Views.Faq}
            />
            <Route
              key="faqform"
              name="faqform"
              exact
              path="/faq/form"
              component={Views.FaqForm}
            />
            <Route
              key="customers"
              name="Customers"
              exact
              path="/customers"
              component={Views.Customers}
            />
            <Route
              key="customersform"
              name="customersform"
              exact
              path="/customers/form"
              component={Views.CustomersForm}
            />
            <Route
              key="numbers"
              name="numbers"
              exact
              path="/numbers"
              component={Views.Numbers}
            />
            <Route
              key="numbersform"
              name="ticketsform"
              exact
              path="/numbers/form"
              component={Views.NumbersForm}
            />
            <Route
              key="series"
              name="series"
              exact
              path="/series"
              component={Views.Series}
            />
            <Route
              key="phrasesmoderate"
              name="phrasesmoderate"
              exact
              path="/phrases/moderate"
              component={Views.PhrasesModerate}
            />
            <Route
              key="phrases"
              name="phrases"
              exact
              path="/phrases"
              component={Views.Phrases}
            />
            <Route
              key="blacklist"
              name="blacklist"
              exact
              path="/blacklist"
              component={Views.Blacklist}
            />
            <Route
              key="blacklistform"
              name="blacklistform"
              exact
              path="/blacklist/form"
              component={Views.BlacklistForm}
            />
            <Route
              key="whitelist"
              name="whitelist"
              exact
              path="/whitelist"
              component={Views.Whitelist}
            />
            <Route
              key="whitelistform"
              name="whitelistform"
              exact
              path="/whitelist/form"
              component={Views.WhitelistForm}
            />
          </>
        )}
      </Switch>
    </ConnectedRouter>
  );
};

export default App;
