import React from 'react';

import { connect } from 'react-redux';

import * as ModalSelectors from '../../store/modal/selectors';
import * as ModalActions from '../../store/modal/actions';

import ModalStyle from './style';

const Modal = ({ modal, closeModal }) => {
  return (
    modal && (
      <ModalStyle modal={modal}>
        <div className="sweet-alert modal-box">
          <div className="info-icon">
            <div className="info-icon-details" />
            <div className="info-icon-symbol" />
          </div>
          <h2>{modal.title}</h2>
          <div className="text-muted lead">{modal.message}</div>
          <button
            className="btn btn-lg btn-info "
            type="button"
            onClick={() => {
              closeModal();
            }}
          >
            Fechar
          </button>
        </div>
      </ModalStyle>
    )
  );
};

const mapStateToProps = state => ({
  modal: ModalSelectors.getModal(state),
});

const mapDispatchToProps = dispatch => ({
  closeModal: () => {
    dispatch(ModalActions.setModal(false));
  },
});

Modal.defaultProps = {
  show: false,
  message: '',
  title: '',
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Modal);
