import React from 'react';

import { connect } from 'react-redux';
import { push } from 'connected-react-router';
import DefaultLayout from '../../layouts/default';
import List from '../../components/List';
import Pagination from '../../components/List/Pagination';
import Search from '../../components/Search';
import ExportCSV from '../../components/ExportCSV';
import * as WhiteListActions from '../../store/whitelist/actions';
import * as WhiteListSelectors from '../../store/whitelist/selectors';

class WhiteList extends React.Component {
  state = {
    pagination: {
      page: 1,
      limit: 10,
    },
  };

  openFormEdit = wl => {
    this.props.setWhitelist(wl);
  };

  componentDidMount() {
    this.getList();
  }

  getList = () => {
    this.props.getList({ ...this.state.pagination, ...this.state.search });
  };

  render() {
    const maxPage = this.state.pagination.page * this.state.pagination.limit;

    return (
      <>
        <Search
          placeholder="Digite uma palavra"
          onSearch={phrase => {
            this.setState({ search: { phrase } }, () => {
              this.getList();
            });
          }}
        />

        <ExportCSV URL="/whitelist" />
        <List
          headers={[{ title: 'Frase/Usuario', field: 'phrase' }]}
          items={this.props.list.items}
          onEdit={this.openFormEdit}
          title="WhiteList"
        />

        <div className="row">
          <div className="col-12">
            <div
              className="dataTables_paginate paging_simple_numbers"
              id="data-table-responsive_paginate"
            >
              <Pagination
                page={this.state.pagination.page}
                totalPages={Math.ceil(
                  this.props.list.total / this.state.pagination.limit
                )}
                changePage={page => {
                  this.setState(
                    { pagination: { ...this.state.pagination, page } },
                    () => {
                      this.getList();
                    }
                  );
                }}
              />
            </div>
          </div>

          <div className="col-12 d-flex justify-content-center">
            <div className="dataTables_info" id="data-table-responsive_info">
              {`Showing ${this.state.pagination.page} to ${maxPage *
                this.state.pagination.page} of ${
                this.props.list.total
              } entries`}
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => ({
  list: WhiteListSelectors.getList(state),
});
const mapDispatchToProps = dispatch => ({
  getList: pagination => {
    const pag = { ...pagination };

    pag.skip = (pagination.page - 1) * pagination.limit;
    delete pag.page;
    dispatch(WhiteListActions.getList(pag));
  },
  setWhitelist: async whitelist => {
    await dispatch(WhiteListActions.setWhitelist(whitelist));

    dispatch(push('/whitelist/form'));
  },
});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(WhiteList)
);
