import queryString from 'query-string';
import * as types from './actionTypes';
import { setLoading } from '../loading/actions';
import { setModal } from '../modal/actions';

export const listLoaded = data => ({
  type: types.LIST_PHRASE_LOADED,
  data,
  loading: false,
});

export const phraseLoaded = data => ({
  type: types.PHRASE_LOADED,
  data,
  loading: false,
});

export const getList = params => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));
    let res;
    try {
      res = await API.get(`/phrase?${queryString.stringify(params)}`);
      dispatch(listLoaded(res.data));
    } catch (e) {
      dispatch(setLoading(false));
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }
  };
};

export const getPhrase = () => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      const res = await API.get(`/phrase/moderate`);
      dispatch(phraseLoaded(res.data));
    } catch (e) {
      dispatch(setLoading(false));
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }
  };
};

export const moderate = (id, params) => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.post(`/moderate/${id}`, params);
      dispatch(setLoading(false));
      dispatch(
        setModal({ message: 'Frase moderada com sucesso', title: 'Pronto' })
      );
    } catch (e) {
      dispatch(setLoading(false));
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }
  };
};
