/* eslint-disable no-param-reassign */
/* eslint-disable no-underscore-dangle */
import React from 'react';

import { connect } from 'react-redux';
import DefaultLayout from '../../layouts/default';
import ModalChoice from '../../components/Modal/choice';
import Form from '../../components/Forms/blacklist';
import * as WhiteListSelectors from '../../store/whitelist/selectors';
import * as WhiteListActions from '../../store/whitelist/actions';

class WhiteListForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data || {},
      showModalDelete: false,
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.data !== this.props.data) {
      this.setState({
        data: this.props.data,
      });
    }
  }

  changeField = (name, value) => {
    this.setState({
      data: { ...this.state.data, [name]: value },
    });
  };

  render() {
    const isNew = !(Object.keys(this.props.data).length > 0);

    return (
      <div className="panel panel-inverse col-6 p-0">
        <div className="panel-heading">
          <p className="panel-title">Formulario</p>
        </div>
        <div className="panel-body">
          <div className="form-group">
            <form
              onSubmit={e => {
                e.preventDefault();
              }}
            >
              <Form
                data={this.state.data}
                changeField={(name, value) => {
                  this.changeField(name, value);
                }}
              />

              <br />

              {isNew ? (
                <button
                  type="submit"
                  className="btn btn-sm btn-primary m-r-5"
                  onClick={e => {
                    e.preventDefault();
                    this.props.createWhiteList(this.state.data);
                  }}
                >
                  Criar
                </button>
              ) : (
                <button
                  type="submit"
                  className="btn btn-sm btn-primary m-r-5"
                  onClick={e => {
                    e.preventDefault();
                    this.props.updateWhiteList(this.state.data);
                  }}
                >
                  Atualizar
                </button>
              )}

              {!isNew && (
                <button
                  type="submit"
                  className="btn btn-sm btn-danger m-r-5"
                  onClick={() => {
                    this.setState({ showModalDelete: true });
                  }}
                >
                  Deletar
                </button>
              )}
            </form>
          </div>
        </div>

        {this.state.showModalDelete && (
          <ModalChoice
            message="Deseja remover o registro selecionado?"
            onConfirm={() => {
              this.props.deleteWhiteList(this.state.data);
            }}
            onCancel={() => {
              this.setState({ showModalDelete: false });
            }}
          />
        )}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  data: WhiteListSelectors.getWhitelist(state),
});

const mapDispatchToProps = dispatch => ({
  createWhiteList: async whitelist => {
    await dispatch(WhiteListActions.createWhiteList(whitelist));
  },
  updateWhiteList: async whitelist => {
    const id = whitelist._id;
    delete whitelist._id;
    await dispatch(WhiteListActions.updateWhiteList(id, whitelist));
  },
  deleteWhiteList: async whitelist => {
    const id = whitelist._id;
    delete whitelist._id;
    whitelist.deleted = true;
    await dispatch(WhiteListActions.updateWhiteList(id, whitelist));
  },
});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(WhiteListForm)
);
