import React from 'react';
import ModalStyle from './style';

class ModalChoice extends React.Component {
  state = {
    disabled: false,
  };

  render() {
    return (
      <ModalStyle modal>
        <div className="modal-choice sweet-alert modal-box">
          <div className="info-icon">
            <div className="info-icon-details" />
            <div className="info-icon-symbol" />
          </div>
          <h2>Atenção</h2>
          <div className="text-muted lead">{this.props.message}</div>
          <button
            className="btn btn-lg btn-danger m-r-5"
            type="button"
            onClick={this.props.onCancel}
          >
            Não
          </button>
          <button
            className="btn btn-lg btn-success m-l-5"
            type="button"
            disabled={this.state.disabled}
            onClick={() => {
              this.props.onConfirm();
              this.setState({ disabled: true });
            }}
          >
            Sim
          </button>
        </div>
      </ModalStyle>
    );
  }
}

export default ModalChoice;
