import React from 'react';

import { connect } from 'react-redux';
import DefaultLayout from '../../layouts/default';
import List from '../../components/List';
import Pagination from '../../components/List/Pagination';
import Search from '../../components/Search';
import * as PhraseActions from '../../store/phrases/actions';
import * as PhraseSelectors from '../../store/phrases/selectors';
import ExportCSV from '../../components/ExportCSV';

class Users extends React.Component {
  state = {
    pagination: {
      page: 1,
      limit: 10,
    },
    search: {},
  };

  componentDidMount() {
    this.getList();
  }

  getList = () => {
    this.props.getList({ ...this.state.pagination, ...this.state.search });
  };

  parseList = list => {
    let parsedList = [];
    if (list) {
      parsedList = list.map(item => ({
        ...item,
        customerCpf: item.customer.cpf,
        customerEmail: item.customer.email,
        customerName: item.customer.name,
      }));
    }

    return parsedList;
  };

  parseCSV = list => {
    let parsedList = [...list];

    if (list) {
      parsedList = list.map(item => {
        const customer = { ...item.customer };
        delete item.customer;

        return {
          ...item,
          customerCpf: customer.cpf,
          customerEmail: customer.email,
          customerName: customer.name,
          customerTelephone: customer.telephone,
        };
      });
    }

    return parsedList;
  };

  render() {
    const maxPage = this.state.pagination.page * this.state.pagination.limit;
    const list = this.parseList(this.props.list.items);
    return (
      <>
        <Search
          placeholder="Digite um cpf"
          onSearch={cpf => {
            this.setState({ search: { 'customer.cpf': cpf } }, () => {
              this.getList();
            });
          }}
        />
        <ExportCSV URL="/phrase" parseList={this.parseCSV} />

        <List
          headers={[
            { title: 'Frase', field: 'phrase' },
            { title: 'Usuario', field: 'socialUser' },
            { title: 'CPF', field: 'customerCpf' },
            { title: 'Email', field: 'customerEmail' },
            { title: 'Status', field: 'status' },
          ]}
          items={list}
          title="Frases"
        />

        <div className="row">
          <div className="col-12">
            <div
              className="dataTables_paginate paging_simple_numbers"
              id="data-table-responsive_paginate"
            >
              <Pagination
                page={this.state.pagination.page}
                totalPages={Math.ceil(
                  this.props.list.total / this.state.pagination.limit
                )}
                changePage={page => {
                  this.setState(
                    { pagination: { ...this.state.pagination, page } },
                    () => {
                      this.props.getList(this.state.pagination);
                    }
                  );
                }}
              />
            </div>
          </div>

          <div className="col-12 d-flex justify-content-center">
            <div className="dataTables_info" id="data-table-responsive_info">
              {`Showing ${this.state.pagination.page} to ${maxPage *
                this.state.pagination.page} of ${
                this.props.list.total
              } entries`}
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => ({
  list: PhraseSelectors.getList(state),
});
const mapDispatchToProps = dispatch => ({
  getList: pagination => {
    const pag = { ...pagination };

    pag.skip = (pagination.page - 1) * pagination.limit;
    delete pag.page;
    dispatch(PhraseActions.getList(pag));
  },
});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Users)
);
