/* eslint-disable no-underscore-dangle */
import React from 'react';

import { connect } from 'react-redux';
import DefaultLayout from '../../layouts/default';

import NumberForm from '../../components/Forms/number';
import * as NumbersSelectors from '../../store/numbers/selectors';

class NumbersForms extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: props.data || {},
      showModalDelete: false,
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.data !== this.props.data) {
      this.setState({
        data: this.props.data,
      });
    }
  }

  render() {
    return (
      <div className="panel panel-inverse col-6 p-0">
        <div className="panel-heading">
          <p className="panel-title">Formulario</p>
        </div>
        <div className="panel-body">
          <div className="form-group">
            <form
              onSubmit={e => {
                e.preventDefault();
              }}
            >
              <NumberForm disabled data={this.state.data} />

              <br />
            </form>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  data: NumbersSelectors.getNumber(state),
});

const mapDispatchToProps = dispatch => ({});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(NumbersForms)
);
