import * as types from './actionTypes';

const initialState = {
  list: [],
  data: {},
};

export default function reduce(state = initialState, action = {}) {
  switch (action.type) {
    case types.LIST_USERS_LOADED:
      return {
        ...state,
        list: action.data,
      };
    case types.SET_USER:
      return {
        ...state,
        data: action.data,
      };
    default:
      return state;
  }
}
