import * as types from './actionTypes';

const initialState = {
  list: [],
  data: {},
  phrase: {},
};

export default function reduce(state = initialState, action = {}) {
  switch (action.type) {
    case types.LIST_PHRASE_LOADED:
      return {
        ...state,
        list: { ...action.data },
      };
    case types.PHRASE_LOADED:
      return {
        ...state,
        phrase: { ...action.data },
      };
    default:
      return state;
  }
}
