import queryString from 'query-string';
import { push } from 'connected-react-router';
import * as types from './actionTypes';
import { setLoading } from '../loading/actions';
import { setModal } from '../modal/actions';

export const listLoaded = data => ({
  type: types.WHITELIST_LOADED,
  data,
  loading: false,
});

export const setWhitelist = data => ({
  type: types.SET_WHITELIST,
  data,
});

export const getList = params => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));
    let res;
    try {
      res = await API.get(`/whitelist?${queryString.stringify(params)}`);
      dispatch(listLoaded(res.data));
    } catch (e) {
      dispatch(setLoading(false));
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }
  };
};

export const createWhiteList = user => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.post(`/whitelist`, user);
      dispatch(push('/whitelist'));
      dispatch(
        setModal({ message: 'Whitelist criado com sucesso', title: 'Pronto' })
      );
    } catch (e) {
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }

    dispatch(setLoading(false));
  };
};

export const updateWhiteList = (id, user) => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.put(`/whitelist/${id}`, user);
      dispatch(push('/whitelist'));
      dispatch(
        setModal({
          message: 'Whitelist atualizado com sucesso',
          title: 'Pronto',
        })
      );
    } catch (e) {
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }

    dispatch(setLoading(false));
  };
};
