import React from 'react';

import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import InputField from '../../components/Inputs/Text';
import LoginStyle from './style';

import Loader from '../../components/Loader';

import * as LoginActions from '../../store/login/actions';
import * as LoginSelectors from '../../store/login/selectors';

class Login extends React.Component {
  state = {
    email: '',
    password: '',
  };

  render() {
    const rota = this.props.role === 'adm' ? 'dashboard' : 'phrases/moderate';
    return (
      <>
        {this.props.token ? (
          <Redirect to={`/${rota}`} />
        ) : (
          <LoginStyle className="container-fluid h-100">
            <div className="row h-100">
              <div className="col-9"></div>
              <div className="col-3 col-fields  h-100">
                <h2>CMS HEINEKEN</h2>
                <label>Email</label>
                <InputField
                  value={this.state.answer}
                  onChange={value => {
                    this.setState({ email: value });
                  }}
                />

                <label>Senha</label>
                <InputField
                  value={this.state.senha}
                  onChange={value => {
                    this.setState({ password: value });
                  }}
                  type="password"
                />

                <button
                  className="btn btn-primary"
                  onClick={() => {
                    this.props.login(this.state);
                  }}
                >
                  Logar
                </button>

                <p className="errors">{this.props.errors}</p>
              </div>
            </div>
            <Loader />
          </LoginStyle>
        )}
      </>
    );
  }
}

const mapStateToProps = state => ({
  errors: LoginSelectors.getError(state),
  token: LoginSelectors.getToken(state),
  role: LoginSelectors.getRole(state),
});
const mapDispatchToProps = dispatch => ({
  login: params => {
    dispatch(LoginActions.login(params));
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);
