import queryString from 'query-string';
import { push } from 'connected-react-router';
import * as types from './actionTypes';
import { setLoading } from '../loading/actions';
import { setModal } from '../modal/actions';

export const listLoaded = data => ({
  type: types.LIST_FAQ_LOADED,
  data,
  loading: false,
});

export const setFaq = data => ({
  type: types.SET_FAQ,
  data,
});

export const getList = params => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));
    let res;
    try {
      res = await API.get(`/faq?${queryString.stringify(params)}`);
      dispatch(listLoaded(res.data));
    } catch (e) {
      dispatch(setLoading(false));
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }
  };
};

export const createFaq = user => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.post(`/faq`, user);
      dispatch(push('/faq'));
      dispatch(
        setModal({ message: 'FAQ criado com sucesso', title: 'Pronto' })
      );
    } catch (e) {
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }

    dispatch(setLoading(false));
  };
};

export const updateFaq = (id, user) => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));

    try {
      await API.put(`/faq/${id}`, user);
      dispatch(push('/faq'));
      dispatch(
        setModal({ message: 'FAQ atualizado com sucesso', title: 'Pronto' })
      );
    } catch (e) {
      dispatch(
        setModal({ title: 'Ops', message: `Ocorreu um erro ${e.message}` })
      );
    }

    dispatch(setLoading(false));
  };
};
