import React from 'react';

import { connect } from 'react-redux';
import { push } from 'connected-react-router';
import moment from 'moment-timezone';
import DefaultLayout from '../../layouts/default';
import List from '../../components/List';
import Pagination from '../../components/List/Pagination';
import Search from '../../components/Search';
import ExportCSV from '../../components/ExportCSV';

import * as CustomersActions from '../../store/customers/actions';
import * as CustomersSelectors from '../../store/customers/selectors';

class Customers extends React.Component {
  state = {
    pagination: {
      page: 1,
      limit: 10,
    },
    search: {},
  };

  componentDidMount() {
    this.getList();
  }

  openFormEdit = customer => {
    this.props.setCustomer(customer);
  };

  getList = () => {
    this.props.getList({ ...this.state.pagination, ...this.state.search });
  };

  parseCSVFull = list => {
    let parsedList = [];
    if (list) {
      parsedList = list.map(item => ({
        cpf: item.customer.cpf,
        'nome cliente': item.customer.name,
        email: item.customer.email,
        aniversario: moment(item.customer.address).format('DD/MM/YYYY'),
        cep: item.customer.cep,
        endereco: item.customer.address,
        complemento: item.customer.complement,
        bairro: item.customer.neighbor,
        cidade: item.customer.city,
        estado: item.customer.state,
        pais: item.customer.country,
        ip: item.customer.ip,
        frase: item.phrase,
        'numero da sorte': item.luckyNumber,
        '@usuario': item.socialUser,
        'status frase': item.status,
        'data moderacao': moment(item.moderateDate).format('DD/MM/YYYY'),
      }));
    }

    return parsedList;
  };

  render() {
    const maxPage = this.state.pagination.page * this.state.pagination.limit;

    return (
      <>
        <Search
          placeholder="Digite um cpf"
          onSearch={cpf => {
            this.setState({ search: { cpf } }, () => {
              this.getList();
            });
          }}
        />

        <ExportCSV URL="/customer" />
        <ExportCSV
          URL="/phrase"
          parseList={this.parseCSVFull}
          label="Exportação Completa"
        />

        <List
          headers={[
            { title: 'Nome', field: 'name' },
            { title: 'Email', field: 'email' },
            { title: 'Cpf', field: 'cpf' },
          ]}
          items={this.props.list.items}
          onEdit={this.openFormEdit}
          title="Clientes"
        />
        <div className="row">
          <div className="col-12">
            <div
              className="dataTables_paginate paging_simple_numbers"
              id="data-table-responsive_paginate"
            >
              <Pagination
                page={this.state.pagination.page}
                totalPages={Math.ceil(
                  this.props.list.total / this.state.pagination.limit
                )}
                changePage={page => {
                  this.setState(
                    { pagination: { ...this.state.pagination, page } },
                    () => {
                      this.getList();
                    }
                  );
                }}
              />
            </div>
          </div>

          <div className="col-12 d-flex justify-content-center">
            <div className="dataTables_info" id="data-table-responsive_info">
              {`Showing ${this.state.pagination.page} to ${maxPage *
                this.state.pagination.page} of ${
                this.props.list.total
              } entries`}
            </div>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => ({
  list: CustomersSelectors.getCustomerList(state),
});
const mapDispatchToProps = dispatch => ({
  getList: params => {
    const pag = { ...params };

    pag.skip = (params.page - 1) * params.limit;
    delete pag.page;
    dispatch(CustomersActions.getList(pag));
  },
  setCustomer: async customer => {
    await dispatch(CustomersActions.setCustomer(customer));

    dispatch(push('/customers/form'));
  },
});

export default DefaultLayout(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Customers)
);
