import Cookies from 'js-cookie';
import * as types from './actionTypes';

const initialState = {
  token: Cookies.get('cmshnk'),
  error: null,
  role: Cookies.get('cmshnkrole'),
};

export default function reduce(state = initialState, action = {}) {
  switch (action.type) {
    case types.SET_TOKEN:
      return {
        ...state,
        token: action.data,
      };
    case types.SET_ERROR:
      return {
        ...state,
        error: action.data,
      };
    case types.SET_ROLE:
      return {
        ...state,
        role: action.data,
      };

    default:
      return state;
  }
}
