import React from 'react';

import className from 'classnames';
import PhraseStyle from './style';

class Phrases extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      accepted: props.value,
    };
  }

  componentDidUpdate(prev) {
    if (this.props.value !== this.state.accepted) {
      this.setState({
        accepted: this.props.value,
      });
    }
  }

  render() {
    return (
      <PhraseStyle>
        <div className="ctn-phrase">
          <div className="note note-secondary note-with-left-icon">
            <div className="note-icon">
              <i className={`fa ${this.props.icon}`}></i>
            </div>
            <div className="note-content text-left">
              <h2>
                <b>{this.props.text}</b>
              </h2>
            </div>
          </div>
          <div className="ctn-buttons">
            <button
              className={`btn btn-lg btn-success ${className({
                active: this.state.accepted,
              })}`}
              onClick={() => {
                this.setState(
                  {
                    accepted: true,
                  },
                  () => {
                    this.props.onChange(this.state.accepted);
                  }
                );
              }}
            >
              <i className="fa fa-check"></i>
            </button>
            <button
              className={`btn btn-lg btn-danger ${className({
                active: !this.state.accepted,
              })}`}
              onClick={() => {
                this.setState(
                  {
                    accepted: false,
                  },
                  () => {
                    this.props.onChange(this.state.accepted);
                  }
                );
              }}
            >
              <i className="fa fa-times"></i>
            </button>
          </div>
        </div>
      </PhraseStyle>
    );
  }
}

export default Phrases;
