import { push } from 'connected-react-router';
import axios from 'axios';
import Cookies from 'js-cookie';
import * as types from './actionTypes';
import { setLoading } from '../loading/actions';

import { setApi } from '../config/actions';
import ENV from '../../env';

export const setToken = data => ({
  type: types.SET_TOKEN,
  data,
  loading: false,
});

export const setRole = data => ({
  type: types.SET_ROLE,
  data,
  loading: false,
});

export const setError = data => ({
  type: types.SET_ERROR,
  data,
  loading: false,
});

export const login = params => {
  return async (dispatch, getState) => {
    const API = getState().config.api;
    dispatch(setLoading(true));
    let res;
    try {
      res = await API.post(`/user/login`, params, {});
      if (res.data.auth) {
        dispatch(
          setApi(
            axios.create({
              baseURL: ENV.API_URL,
              headers: {
                Authorization: `Bearer ${res.data.token}`,
                'Content-Type': 'application/json',
                Accept: 'application/json',
              },
            })
          )
        );

        Cookies.set('cmshnk', res.data.token, { expires: 1 });
        Cookies.set('cmshnkrole', res.data.role, { expires: 1 });
        dispatch(setToken(res.data.token));
        dispatch(setRole(res.data.role));
        dispatch(setLoading(false));

        if (res.data.role === 'adm') {
          dispatch(push('/dashboard'));
        } else {
          dispatch(push('/phrases/moderate'));
        }
      } else {
        dispatch(setError('CREDENCIAIS INVALIDAS'));
      }
    } catch (e) {
      dispatch(setError('ERROR AO TENTAR LOGAR '));
      dispatch(setLoading(false));
    }
  };
};

export const logout = params => {
  return async (dispatch, getState) => {
    dispatch(
      setApi(
        axios.create({
          baseURL: ENV.API_URL,
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
        })
      )
    );

    Cookies.remove('cmshnk');
    Cookies.remove('cmshnkrole');

    dispatch(setToken(null));
    dispatch(setLoading(false));
    dispatch(push('/'));
  };
};
