import * as types from './actionTypes';

const initialState = {
  list: [],
  data: {},
};

export default function reduce(state = initialState, action = {}) {
  switch (action.type) {
    case types.LIST_SERIES_LOADED:
      return {
        ...state,
        list: action.data,
      };

    default:
      return state;
  }
}
