import axios from 'axios';
import Cookies from 'js-cookie';
import * as types from './actionTypes';

import ENV from '../../env';

const cookies = Cookies.get('cmshnk');
const initialState = {
  api: axios.create({
    baseURL: ENV.API_URL,
    headers: {
      ...(cookies ? { Authorization: `Bearer ${cookies}` } : {}),
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
  }),
};

export default function reduce(state = initialState, action = {}) {
  switch (action.type) {
    case types.SET_API:
      return {
        ...state,
        api: action.data,
      };
    default:
      return state;
  }
}
