import React from 'react';
import { connect } from 'react-redux';
import { Parser } from 'json2csv';
import * as ConfigSelectors from '../../store/config/selectors';

class ExportCSV extends React.Component {
  getItems = async () => {
    const res = await this.props.axios.get(this.props.URL);
    let list = res.data.items;
    if (this.props.parseList) {
      list = this.props.parseList(list);
    }
    this.exportToCSV(list);
  };

  exportToCSV = arrayOfObjects => {
    if (!arrayOfObjects.length) {
      return;
    }

    const parsercsv = new Parser({
      expandArrayObjects: false,
    });
    let csvContent = 'data:text/csv;charset=utf-8,';
    csvContent += parsercsv.parse(arrayOfObjects);

    const encodedUri = encodeURI(csvContent);
    const link = document.createElement('a');
    link.setAttribute('href', encodedUri);
    link.setAttribute('download', `export_${new Date()}.csv`);
    document.body.appendChild(link); // Required for FF
    link.click();
    document.body.removeChild(link);
  };

  render() {
    return (
      <button
        className="btn btn-success"
        style={{ marginBottom: 20, marginRight: 10 }}
        onClick={this.getItems}
      >
        <i className="fa fa-file-download fa-fw" style={{ fontSize: 18 }}></i>
        {this.props.label || 'Exportar'}
      </button>
    );
  }
}

ExportCSV.defaultProps = {};

const mapStateToProps = state => ({
  axios: ConfigSelectors.getApi(state),
});
const mapDispatchToProps = dispatch => ({});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ExportCSV);
